import React from "react";
import { Component } from "react";

export class CodeBlock extends Component {
  constructor(props) {
    super();

    this.state = {};
    this.state.codeLines = props.lines;
    this.state.codeResult = props.result;
  }

  createCodeItems = (lines) => {
    return lines.map((l, i) => (
      <li className="tp-code" key={i}>
        <pre>{l}</pre>
      </li>
    ));
  };

  createCodeResultItems = (lines) => {
    return lines.map((l, i) => (
      <li className="tp-code result" key={i}>
        <pre>{l}</pre>
      </li>
    ));
  };

  render = () => {
    return (
      <div className="tp-code_block" id="tp-code_1">
        <div className="tp-code_container">
          <div className="tp-code_box">
            <div className="dots">
              <div className="dot-red"></div>
              <div className="dot-yellow"></div>
              <div className="dot-purple"></div>
            </div>

            <ul className="tp-code_list">
              {this.createCodeItems(this.state.codeLines)}
            </ul>

            <ul className="tp-code_list result_list">
              <p className="result-title">Result: </p>
              {this.createCodeResultItems(this.state.codeResult)}
            </ul>
          </div>
        </div>
      </div>
    );
  };
}
